import * as types from './mutations';

export const refreshState = ({ commit }) => {

    axios.get('/api/timecards')
        .then((response) => {
            commit('SET_STATE_TIMECARDS', { timecards: response.data })
        }, (error) => {
            console.log(error);
        });
    
    axios.get('/api/users')
        .then((response) => {
            commit('SET_STATE_USERS', { users: response.data });
        }, (error) => {
            console.log(error);
        });
    
    axios.get('/api/projects')
        .then((response) => {
            commit('SET_STATE_PROJECTS', { projects: response.data });
        }, (error) => {
            console.log(error);
        });
    
    axios.get('/api/roles')
        .then((response) => {
            commit('SET_STATE_ROLES', { roles: response.data });
        }, (error) => {
            console.log(error);
        });
}